import { useState, useCallback } from "react";
import { Box, Text, DataTable } from "grommet";
import { Upload, Image, DocumentPdf } from "grommet-icons";
import Card from "./components/Card";
import { useDropzone } from "react-dropzone";
import { Document, Page, pdfjs } from "react-pdf";

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

const Docs = () => {
  const [numPages, setNumPages] = useState(null);
  const [pageNumber] = useState(1);
  const [pdf, setPdf] = useState(null);
  const [file, setFile] = useState(null);
  const [select, setSelect] = useState([]);

  function onDocumentLoadSuccess({ numPages }) {
    setNumPages(numPages);
  }

  const onDrop = useCallback((acceptedFiles) => {
    setFile(acceptedFiles);
  }, []);
  const { getRootProps, getInputProps } = useDropzone({ onDrop });

  const columns = [
    {
      property: 'name',
      header:<Text>File</Text>,
      render:datum=>datum.name.replace(/\.[^.]+$/, "")
    },
    {
      property: 'type',
      header:<Text>Format</Text>,
      render: datum=>{
        const type = datum.type.split('/')
        if (type[0] === 'image') return <Image color="brand" />
        if (type[1] === 'pdf') return <DocumentPdf color="brand" />
      }
    },
    {
      property: 'size',
      header:<Text>kb</Text>,
      render:datum => Math.round(datum.size / 1024),
    },
    {
      property: 'lastModified',
      header:<Text>Ver</Text>,
      render:datum => new Date(datum.lastModified).toLocaleDateString('en-GB')
    },    
  ]

  return (
  <>
      <Box
        focusIndicator={false}
        pad={{ top: "xlarge", horizontal: "large" }}
        justify="center"
        align="center"
        {...getRootProps()}
      >
        {file ? (
          <DataTable
        columns={columns}
        data={file}
        step={10}
        select={select}
        onSelect={(item)=>{
          try {
            const fileType = item[item.length -1].split('.')
            fileType[1] === 'pdf' ? setSelect(item) : console.log('needs error message')
          } catch {
            console.log('needs error message')
          }
        }}
        onClickRow={event => setPdf(event.datum) }
      />
        ) : (
          <>
            <input {...getInputProps()} />
            <Card
              title="Upload file"
              subTitle="Click or drag'n'drop here"
              children={<Upload size="large" />}
              footer=""
              size="large"
            />
          </>
        )}
      </Box>
      { pdf ? <Box pad="small" align="center">
        <Card
          title=""
          subTitle=""
          children={
            <Document file={pdf} onLoadSuccess={onDocumentLoadSuccess}>
              <Page pageNumber={pageNumber} />
            </Document>
          }
          footer={
            numPages? <Text>{numPages} pages</Text> : null
          }
          size="large"
        />
      </Box> : 
      
      
      <Box pad="large">
         {file? <Card
          title="Click on row"
          subTitle="to preview and select it to upload"
          children={null}
          footer=""
          size="large"
        /> : null }
      </Box> }
    </>
  );
};

export default Docs;
