import { useState } from "react";
import { Grommet, Box, Grid, ResponsiveContext } from "grommet";
import { grommet } from "grommet/themes";
import { deepMerge } from "grommet/utils";
import themeMods from "./themeMods";

import Header from "./Header";
import Main from "./Main";
import Side from "./Side";
import Footer from "./Footer";

const theme = deepMerge(grommet, themeMods);

const columns = {
  small: ["auto"],
  medium: ["2/3", "1/3"],
  large: ["2/3", "1/3"],
};

const gridAreas = {
  small: [["header"], ["main"], ["side"], ["footer"]],
  medium: [
    ["header", "header"],
    ["main", "side"],
    ["footer", "footer"],
  ],
  large: [
    ["header", "header"],
    ["main", "side"],
    ["footer", "footer"],
  ],
};

const rows = {
  small: ["xsmall"],
  medium: ["xsmall"],
  large: ["xsmall"],
};

const Responsive = ({
  children,
  overrideColumns,
  overrideRows,
  areas,
  ...props
}) => (
  <ResponsiveContext.Consumer>
    {(size) => {
      let columnsVal = columns;
      if (columns) {
        if (columns[size]) {
          columnsVal = columns[size];
        }
      }

      let rowsVal = rows;
      if (rows) {
        if (rows[size]) {
          rowsVal = rows[size];
        }
      }

      let areasVal = areas;
      if (areas && !Array.isArray(areas)) areasVal = areas[size];

      return (
        <Grid
          {...props}
          areas={!areasVal ? undefined : areasVal}
          columns={!columnsVal ? size : columnsVal}
          rows={!rowsVal ? size : rowsVal}
        >
          {children}
        </Grid>
      );
    }}
  </ResponsiveContext.Consumer>
);

const App = () => {
  const [darkMode, setDarkMode] = useState(true);
  const [link, linkTo] = useState(<Main />);
  return (
    <Grommet full theme={theme} themeMode={darkMode ? "dark" : "light"}>
      <Responsive columns={columns} rows={rows} gap="xsmall" areas={gridAreas}>
        <Box background="brand" gridArea="header">
          <Header darkMode={darkMode} setMode={setDarkMode} link={linkTo} />
        </Box>

        <Box gridArea="main">{link}</Box>

        <Box gridArea="side">
          <Side />
        </Box>

        <Box gridArea="footer">
          <Footer />
        </Box>
      </Responsive>
    </Grommet>
  );
};

export default App;
