import {
  CheckBox,
  Button,
  Box,
  Header as Head,
  Menu,
  ResponsiveContext,
} from "grommet";
import { Home, Apps, Copy } from "grommet-icons";

import Docs from "./Docs";
import Main from "./Main";

const Header = ({ darkMode, setMode, link }) => {
  const onChange = (event) => setMode(event.target.checked);

  const burgerMenuItems = [
    {
      label: (
        <Button
          icon={<Copy />}
          reverse
          plain
          label="Docs"
          onClick={() => link(<Docs />)}
        />
      ),
    },
    {
      label: (
        <CheckBox
          reverse
          label="Dark"
          checked={darkMode}
          onChange={onChange}
          toggle
        />
      ),
    },
  ];

  return (
    <Head background="brand" pad="medium" height="xsmall">
      <ResponsiveContext.Consumer>
        {(size) =>
          size !== "small" ? (
            <>
              <Button
                as="span"
                icon={<Home />}
                label="conveynce me"
                onClick={() => link(<Main />)}
              />
              <Box justify="start">
                <Button
                  icon={<Copy />}
                  reverse
                  plain
                  onClick={() => link(<Docs />)}
                />
              </Box>

              <Box justify="end">
                <CheckBox
                  label="dark"
                  checked={darkMode}
                  onChange={onChange}
                  toggle
                />
              </Box>
            </>
          ) : (
            <>
              <Button
                as="span"
                icon={<Home />}
                onClick={() => link(<Main />)}
              />
              <Menu
                a11yTitle="Navigation Menu"
                dropProps={{ align: { top: "bottom", right: "right" } }}
                icon={<Apps color="#1e1d23" />}
                items={burgerMenuItems}
                dropBackground="#1e1d23"
                size="medium"
              />
            </>
          )
        }
      </ResponsiveContext.Consumer>
    </Head>
  );
};

export default Header;
